export class Vector2D {
    constructor(x, y) {
        if (x === undefined) {
            x = 0;
        }
        if (y === undefined) {
            y = 0;
        }
        this.x = x;
        this.y = y;
    }
    toString() {
        return `x: ${this.x}, y: ${this.y}`;
    }
    static add(...vectors) {
        return vectors.reduce((previous, current) => {
            return { x: previous.x + current.x, y: previous.y + current.y };
        }, { x: 0, y: 0 });
    }
    /**
     * @returns v1 - v2
     */
    static subtract(v1, v2) {
        return { x: v1.x - v2.x, y: v1.y - v2.y };
    }
    static scale(v, scalar) {
        return { x: v.x * scalar, y: v.y * scalar };
    }
    static dotProduct(v1, v2) {
        return v1.x * v2.x + v1.y * v2.y;
    }
    static magnitude(v) {
        return Math.sqrt(v.x * v.x + v.y * v.y);
    }
    static normalize(v) {
        const mag = this.magnitude(v);
        return mag === 0 ? { x: 0, y: 0 } : this.scale(v, 1 / mag);
    }
    /**
     * @param v1 Vector2D
     * @param v2 Vector2D
     * @returns Distance from v1 to v2
     */
    static distance(v1, v2) {
        return Math.sqrt((v2.x - v1.x) ** 2 + (v2.y - v1.y) ** 2);
    }
    /**
     * returns a vector pointing from v1 to v2
     */
    static displacementVector(v1, v2) {
        return this.subtract(v2, v1);
    }
    static hadamardProduct(v1, v2) {
        return { x: v1.x * v2.x, y: v1.y * v2.y };
    }
}
